body {
  background-color: #0f1012;
  padding: 15vh 0 8vh;
}

main {
  position: relative;
  font-family: 'Outfit', sans-serif;
  height: 77vh;
  width: 65%;
  min-width: 800px;
  max-width: 1000px;
  background-color: #ddd;
  margin: 0 auto;
  display: flex;
}

.loading {
  display: none;
  width: 360px;
  height: 200px;
  margin: 10px;
  border: 4px solid;
  border-radius: 10px;
  font-size: 40px;
  background-size: cover;
  justify-content: center;
  align-items: center;
}

.loading.show {
  display: block;
}

.search input {
  width: 180px;
  background-color: #d0d0d0;
  border: none;
  padding: 10px 30px 10px 10px;
  border-radius: 50px;
  box-sizing: border-box;
}

.search-inner {
  position: relative;
  display: inline-block;
}

.search button {
  position: absolute;
  top: 7px;
  right: 10px;
}

.search select {
  width: 80px;
  padding: .8em .5em;
  font-family: inherit;
  background-color: transparent;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0px;
}

.text-content {
  position: relative;
  width: 350px;
}

.text-content::before {
  content: '';
  width: 150px;
  height: 75px;
  position: absolute;
  left: -75px;
  background: url(../images/bg01.png) no-repeat;
  background-size: 100%;
  transform: rotate(180deg);
}

.text-content::after {
  content: '';
  width: 24px;
  height: 82px;
  position: absolute;
  top: 50%;
  left: 20px;
  margin-top: -41px;
  background: url(../images/bg02.png) no-repeat;
  background-size: 100%;
}

.text-content ul {
  padding: 110px 30px 30px 100px;
  height: 100%;
  box-sizing: border-box;
}

.text-content li:last-child {
  margin-top: 50px;
}

.text-content h2 {
  font-size: 23px;
  font-weight: 800;
}

.text-content h2::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #000;
  margin: 15px 0;
}

.text-content span {
  display: block;
  font-size: 13px;
  font-weight: 800;
  margin-bottom: 18px;
}

.text-content p {
  font-size: 13px;
  line-height: 24px;
  letter-spacing: -0.8px;
}

.movies-content {
  display: flex;
  flex-direction: column;
}

.movies {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding: 10px;
}

.movie {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
}

.movie h1 {
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  margin: auto;
  text-align: center;
  padding: 20px 0;
}

.more {
  display: none;
  font-size: 35px;
  padding: 20px 40px;
  background-color: darkkhaki;
  border-radius: 50px;
  margin: 20px auto;
}

.more.show {
  display: block;
}