body {
  background-color: #0f1012;
  padding: 15vh 0 8vh;
}

main {
  height: 77vh;
  width: 65%;
  min-width: 800px;
  max-width: 1000px;
  background-color: #ddd;
  margin: 0 auto;
  font-family: Outfit, sans-serif;
  display: flex;
  position: relative;
}

.loading {
  width: 360px;
  height: 200px;
  background-size: cover;
  border: 4px solid;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 10px;
  font-size: 40px;
  display: none;
}

.loading.show {
  display: block;
}

.search input {
  width: 180px;
  box-sizing: border-box;
  background-color: #d0d0d0;
  border: none;
  border-radius: 50px;
  padding: 10px 30px 10px 10px;
}

.search-inner {
  display: inline-block;
  position: relative;
}

.search button {
  position: absolute;
  top: 7px;
  right: 10px;
}

.search select {
  width: 80px;
  appearance: none;
  background-color: #0000;
  border: none;
  border-radius: 0;
  padding: .8em .5em;
  font-family: inherit;
}

.text-content {
  width: 350px;
  position: relative;
}

.text-content:before {
  content: "";
  width: 150px;
  height: 75px;
  background: url("bg01.8bf39b66.png") 0 0 / 100% no-repeat;
  position: absolute;
  left: -75px;
  transform: rotate(180deg);
}

.text-content:after {
  content: "";
  width: 24px;
  height: 82px;
  background: url("bg02.00bf9aa0.png") 0 0 / 100% no-repeat;
  margin-top: -41px;
  position: absolute;
  top: 50%;
  left: 20px;
}

.text-content ul {
  height: 100%;
  box-sizing: border-box;
  padding: 110px 30px 30px 100px;
}

.text-content li:last-child {
  margin-top: 50px;
}

.text-content h2 {
  font-size: 23px;
  font-weight: 800;
}

.text-content h2:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #000;
  margin: 15px 0;
  display: block;
}

.text-content span {
  margin-bottom: 18px;
  font-size: 13px;
  font-weight: 800;
  display: block;
}

.text-content p {
  letter-spacing: -.8px;
  font-size: 13px;
  line-height: 24px;
}

.movies-content {
  flex-direction: column;
  display: flex;
}

.movies {
  flex-wrap: wrap;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.movie {
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.movie h1 {
  text-align: center;
  margin: auto;
  padding: 20px 0;
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
}

.more {
  background-color: #bdb76b;
  border-radius: 50px;
  margin: 20px auto;
  padding: 20px 40px;
  font-size: 35px;
  display: none;
}

.more.show {
  display: block;
}

